@import "../core/stylesheets/core/variables";
@import "../core/stylesheets/core/mixins";
[class^="atoms-"], [class^="molecules-"], [class^="organisms-"], [id^="atoms-"], [id^="molecules-"], [id^="organisms-"] {
	max-width: 1020px;
	padding: 0 20px;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	margin: 0 auto;
}

[class^="flyout-"] ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

.sg-table-colors {
	th, td {
		border: 1px solid #b8c0c6;
		text-align: center;
	}
	th {
		background-color: #d0d5d9;
	}
	td {
		vertical-align: middle;
		&:first-child {
			vertical-align: top;
			text-align: left;
		}
		p:last-child {
			margin-bottom: 0;
		}
	}
	.sg-swatch {
		width: 80px;
		height: 80px;
		margin: 0 auto !important;
	}
}

[class^="molecules-"] {
	.table-tips {
		.drag-tip {
			opacity: 0;
		}
	}
}

.sg-icons-list {
	a {
		display: block;
		color: $text-color;
		width: 100%;
		height: 100%;
		padding: 5px 10px;
		cursor: pointer;

		i {
			display: inline-block;
			font-size: 14px;
			line-height: 22px;
			color: $text-color;

			&:before {
				font-size: 1em;
			}
		}

		&:hover {
			background-color: $link-color;
			color: white;
			text-decoration: none;

			i {
				@include vendor-prefix(transform, scale(2));
				margin-right: 10px;
				color: white;
			}

			span {
				text-decoration: none;
			}
		}
	}
}
